.searchbar-container {
  background-color: rgba(0, 0, 0, 0.4);
  margin: 0 auto;
  width: 100%;
  height: 60px;
}

.form-container {
  margin: 0 auto;
  width: 500px;
  height: 100%;
  display: flex;
  align-items: center;
}

.search-form {
  width: 80%;
  margin: 0 auto;
  display: flex;

  &__input,
  &__button {
    background: rgba(255, 255, 255, 0.082);
    width: 300px;
    padding: 1em;
    border: none;
    border-radius: 5000px;
    backdrop-filter: blur(5px);
    box-shadow: 4px 4px 60px rgba(0, 0, 0, 0.2);
    color: #fff;
    font-family: Montserrat, sans-serif;
    font-weight: 500;
    transition: all 0.2s ease-in-out;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);

    &:hover {
      background: rgba(255, 255, 255, 0.1);
      box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
    }

    &:focus {
      background: rgba(255, 255, 255, 0.1);
      box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
    }
  }

  &__button {
    margin-left: 10px;
    width: 120px;
    font-size: 1rem;
    color: rgba(255, 255, 255, 0.5);
    &:hover {
      cursor: pointer;
    }

    &:active {
      background: rgba(255, 255, 255, 0.2);
    }
  }
}

.morebutton-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.morebutton {
  background: rgba(255, 255, 255, 0.082);
  width: 300px;
  padding: 1em;
  border: none;
  border-radius: 5000px;
  backdrop-filter: blur(5px);
  box-shadow: 4px 4px 60px rgba(0, 0, 0, 0.2);
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  transition: all 0.2s ease-in-out;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  width: 120px;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.5);
  align-items: center;
  margin: 0 auto 30px;

  &:hover {
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
    cursor: pointer;
  }

  &:focus {
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 4px 4px 60px 8px rgba(0, 0, 0, 0.2);
  }

  &:active {
    background: rgba(255, 255, 255, 0.2);
  }
}
